import {
  ConstructorElement,
  DragIcon,
} from '@ya.praktikum/react-developer-burger-ui-components';
import BurgerConstuctorStyle from '../burger-constructor/burger-constructor.module.css';
import { useRef } from 'react';
import {
  useDrop,
  useDrag,
  DropTargetMonitor,
  XYCoord,
  DragSourceMonitor,
} from 'react-dnd';
import { DELETE_COMPONENT } from '../../services/actions/conductor';
import { Iingredient } from '../../utils/types';
import { useDispatch } from 'react-redux';
interface OrderedIngredient extends Iingredient {
  dragId: string;
  index: number | undefined;
}
export default function OrderedIngredient({
  item,
  index,
  moveCard,
}: {
  item: OrderedIngredient;
  index: number;
  moveCard: Function;
}) {
  const dispatch = useDispatch();
  const ref = useRef<HTMLElement>(null);
  const [{ handlerId }, drop] = useDrop({
    //    ,  dnd ,
    //       ,    .
    //         
    accept: 'component',
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId(),
      };
    },
    // ,      ,
    //        props.index
    hover(item: any, monitor: DropTargetMonitor) {
      if (!ref.current) {
        return;
      }
      //     
      const dragIndex = item.index;
      const hoverIndex = index;

      //   ,   
      if (dragIndex === hoverIndex) {
        return;
      }
      //    
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      //      Y  
      //     ,  
      //     
      const hoverMiddleY =
        (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      //    ,
      //   
      const clientOffset = monitor.getClientOffset();
      //       
      //       
      const hoverClientY = (clientOffset as XYCoord).y - hoverBoundingRect.top;
      //      
      //     
      //  ,       moveCard
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return;
      }
      //      
      //      ,    
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return;
      }
      //        
      moveCard(dragIndex, hoverIndex);
      //      
      //     
      item.index = hoverIndex;
    },
  });
  //       
  //  
  const [{ isDragging }, drag] = useDrag({
    type: 'component',
    item: () => ({ id: item._id, index }),
    collect: (monitor: DragSourceMonitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });
  //     0,
  //        
  const opacity = isDragging ? 0 : 1;
  //           :)
  if (item.type !== 'bun') drag(drop(ref));
  //     onDrop
  //           
  const preventDefault = (e: React.SyntheticEvent) => e.preventDefault();
  return (
    <div
      ref={ref as React.RefObject<HTMLDivElement>}
      style={{ opacity }}
      className={`${BurgerConstuctorStyle.IngredientField} mb-4  mr-2`}
      onDrop={preventDefault}
      data-handler-id={handlerId}
    >
      <DragIcon type="secondary" />
      <ConstructorElement
        text={item.name}
        price={item.price}
        thumbnail={item.image}
        handleClose={() => dispatch({ type: DELETE_COMPONENT, action: item })}
        key={item._id}
      />
    </div>
  );
}
